<template>
	<div class="card">
		<headerNav title="购物车"/>

		<div v-if="shoppingCart.length === 0" class="product-not-found-container">
			<div class="product-not-found-img">
				<img src="../../static/img/goodsList/notfound.png" width="30%" height="30%">
			</div>
			<div class="product-not-found-txt">购物车空空如也</div>
		</div>

		<template v-else>
			<van-cell class="head">
				<template slot="title">
					<van-checkbox v-model="checkedAll" @change="toggleCheckAll">全选</van-checkbox>
				</template>
				<template slot="right-icon">
					<div @click="deleteSelect" style="display: flex;">
						<span>删除选中</span>
						<van-icon name="delete" class="custom-icon" size="18px"/>
					</div>
				</template>
			</van-cell>

			<van-cell title="商品总量" :value="totalCount"/>

			<van-checkbox-group class="card-goods" v-model="checkedGoods" @change="toggleOne">
				<div class="promotion-group">
					<div v-for="(item,index) in shoppingCart" :key="index" class="card-goods__item">
						<van-checkbox :name="index"></van-checkbox>

						<product-card :product='item' :isCard='true'>
							<template slot>
								<van-cell :title="`小计:￥${item.totalPrice}`">
									<template slot="right-icon">
										<div @click="deleteItem(index)" style="display: flex;">
											<span>删除商品</span>
											<van-icon name="delete" class="custom-icon" size="18px"/>
										</div>
									</template>
								</van-cell>
							</template>
						</product-card>
					</div>
				</div>
			</van-checkbox-group>

			<van-submit-bar style="bottom: 50px;" :price="totalPrice" button-text="去下单" label='总金额：' @submit="onSubmit"/>
		</template>

		<navigate/>
		<div style="height: 50px;"></div>
	</div>
</template>

<script>
	import {Dialog} from 'vant';

	import headerNav from '../components/header/nav'
	import navigate from '../components/footer/navigate.vue'
	import productCard from '../components/product/productcard'
	import {mapActions, mapState} from "vuex";

	export default {
		name: 'Cart',

		components: {
			headerNav,
			navigate,
			productCard
		},

		data() {
			return {
				checkedAll: false,
				checkedGoods: [],
			};
		},

		computed: {
			...mapState(["shoppingCart"]),

			totalCount: {
				get() {
					let total = 0;
					this.shoppingCart.forEach(item => {
						total += item.GoodsCount;
					});
					return total;
				}
			},

			totalPrice: {
				get() {
					let total = 0;
					this.shoppingCart.forEach(item => {
						total += item.GoodsCount * item.Price;
					});
					return total * 100;
				}
			},
		},

		async mounted() {
			this.$store.commit('SET_TABBAR_INDEX', 2);
			await this.loadShoppingCart();
		},

		methods: {
			...mapActions(["loadShoppingCart"]),

			toggleCheckAll() {
				if (this.checkedAll) {
					this.checkedGoods = [];
					for (let i = 0; i < this.shoppingCart.length; i++) this.checkedGoods.push(i);
				} else {
					if (this.checkedGoods.length === this.shoppingCart.length) this.checkedGoods = [];
				}
			},

			toggleOne() {
				this.checkedAll = this.checkedGoods.length === this.shoppingCart.length;
			},

			deleteSelect() {
				if (this.checkedGoods.length === 0) {
					this.$Message.error("请先选择要删除的商品");
					return;
				}

				Dialog.confirm({
					title: '购物车',
					message: '确认删除选中的商品？'
				}).then(async () => {
					let data = [];
					this.checkedGoods.forEach(index => {
						data.push(this.shoppingCart[index].CartID);
					});
					let res = await this.$request.deleteCart(data);
					if (res) {
						this.$Message.info("删除成功");
						this.checkedAll = false;
						this.checkedGoods = [];
						await this.loadShoppingCart();
					}
				}).catch(() => {});
			},

			deleteItem(index) {
				Dialog.confirm({
					title: '购物车',
					message: '确认删除此商品？'
				}).then(async () => {
					let res = await this.$request.deleteCart([this.shoppingCart[index].CartID]);
					if (res) {
						this.$Message.info("删除成功");
						this.checkedAll = false;
						this.checkedGoods = [];
						await this.loadShoppingCart();
					}
				}).catch(() => {});
			},

			onSubmit() {
				this.$router.push('/order');
			}
		}
	};
</script>

<style lang="less">
	.card-goods {
		font-size: 12px;
		&__item {
			position: relative;
			.van-checkbox {
				width: 20px;
				height: 20px;
				top: 40px;
				left: 5px;
				z-index: 1;
				position: absolute;
			}
			.additional {
				width: 100%;
				padding-left: 15px;
				box-sizing: border-box;
			}
		}
	}

	.head {
		padding-left: 5px;
		border-bottom: 1px solid #eee;
	}

	.card {
		/*background: #f7f7f7;*/
		.van-submit-bar__bar {
			border-top: 1px solid #f7f7f7;
			.van-checkbox {
				padding-left: 5px;
			}
		}
		.promotion {
			.van-tag {
				line-height: 12px;
				margin-right: 5px;
			}
			.van-cell__title {
				flex: 4;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}
		.promotion-group {
			box-shadow: 2px 2px 2px #e5e5e5;
		}
	}

	.custom-icon {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.product-not-found-container {
		margin-top: 40%;
		width: 100%;
		height: 100%;
	}

	.product-not-found-img {
		margin-top: 15px;
		margin-bottom: 15px;
		text-align: center;
	}

	.product-not-found-txt {
		text-align: center;
		font-size: 14px;
		color: #8c8c8c;
	}
</style>


